import React from "react";
import ReactModal from "react-modal";
import LayoutModal from "./LayoutModal";
import FieldsModal from "./FieldsModal";
import FieldSettings from "./FieldSettings";
import UpgradeModal from "./UpgradeModal";
import "./index.css";

const Modal = (props) => {
	const modalProps = props.modalProps;
	let modal = "";
	//console.log(modalProps.type); return;
	if (modalProps.type == "layout") {
		modal = <LayoutModal modalProps={props.modalProps} />;
	} else if (modalProps.type == "addField") {
		modal = <FieldsModal modalProps={props.modalProps} />;
	} else if (modalProps.type == "fieldSettings") {
		modal = <FieldSettings modalProps={props.modalProps} />;
	} else if (modalProps.type == "upgradeModal") {
		modal = <UpgradeModal modalProps={props.modalProps} />;
	}

	return (
		<ReactModal
			ariaHideApp={false}
			shouldCloseOnEsc={true}
			shouldCloseOnOverlayClick={true}
			portalClassName={`modal-portal ${modalProps.type}`}
			className={`modal-container ${modalProps.type}`}
			role="dialog"
			parentSelector={() => document.getElementById("views-container")}
			isOpen={modalProps.isVisible}
		>
			{modalProps.type !== "upgradeModal" && (
				<div className="layoutHead">
					<h2 className="layoutHeading">{modalProps.label} </h2>

					<span onClick={modalProps.closeModal} className="closeLayoutOption">
						<i className="fas fa-times" />
					</span>

					{modalProps.activeSection === "loop" && modalProps.type == "fieldSettings" && (
						<div className="fieldDetails">
							{!isNaN(modalProps.formFieldId) && (
								<div className="fieldDetailsCol">
									<span className="fieldDetailLabel">FieldID:</span>
									<span>#{modalProps.formFieldId}</span>
								</div>
							)}
							<div className="fieldDetailsCol">
								<span className="fieldDetailLabel">Type:</span>
								<span>{modalProps.fieldType}</span>
							</div>
						</div>
					)}
				</div>
			)}

			{modalProps.type === "upgradeModal" && (
				<span onClick={modalProps.closeModal} className="closeLayoutOption upgradeModalClosBtn">
					<i className="fas fa-times" />
				</span>
			)}

			{modal}
		</ReactModal>
	);
};

export default Modal;
