import React from "react";
import { Draggable } from "react-beautiful-dnd";
import { FieldIcons } from "../../../../FieldIcons";
import "./index.css";

const Field = (props) => {
	const fieldProps = props.fieldProps;
	const fieldId = props.fieldId;
	const fieldSettings = props.allFields[fieldId].fieldSettings;
	const label = fieldSettings.useCustomLabel ? fieldSettings.label : props.allFields[fieldId].label;

	let fieldIcon = "fa-link";
	const formFieldId = props.allFields[fieldId].formFieldId;
	let fieldType = formFieldId;

	if (typeof props.formFields[formFieldId] !== "undefined") {
		fieldType = props.formFields[formFieldId].fieldType;
	}
	if (typeof FieldIcons[fieldType] !== "undefined") {
		fieldIcon = FieldIcons[fieldType];
	}

	return (
		<Draggable type={props.sectionName} draggableId={fieldId} index={props.index}>
			{(provided, snapshot) => {
				const draggableClasses = snapshot.isDragging ? "activeField" : "";
				return (
					<div className={`${draggableClasses} field`} {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>
						<span title="Settings" className="settings" onClick={(event) => props.openModal(event, "fieldSettings", props.allFields[fieldId].label, fieldId, props.sectionName)}>
							<i className="fas fa-cog" />
						</span>
						<i className={`fas ${fieldIcon}`} />
						{label}
						<span className="close" onClick={(event) => fieldProps.removeField(event, fieldId, props.colId)}>
							<i className="fas fa-trash-alt" />
						</span>
					</div>
				);
			}}
		</Draggable>
	);
};

export default Field;
