import React from 'react';

const InlineEditSettings = (props) => {
	const viewSettingsProps = props.viewSettingsProps;
	const viewSettings = viewSettingsProps.viewSettings;
	if (typeof viewSettings.inlineEdit === 'undefined') {
		viewSettings.inlineEdit = {
			enable: viewSettings.multipleentries.inlineEdit || false,
			editMode: 'popup',
			style: 'jqueryui',
			enableLabel: 'Enable Inline Edit',
			disableLabel: 'Disable Inline Edit',
		};
	}

	return (
		<div className='formatted-settings'>
			<div className='pure-form settings-row'>
				<label className='inline-label'>
					<span>Enable</span>
				</label>
				<input name='enable' type='checkbox' disabled checked={viewSettings.inlineEdit.enable || false} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'enable')} />
				<span>Enable Inline Edit</span>
			</div>

			<div className='pure-form settings-row'>
				<label className='inline-label'>
					<span>Style</span>
				</label>
				<input name='style' type='radio' disabled value='plain' checked={viewSettings.inlineEdit.style === 'plain'} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'style')} /> <span className='radio-label'>Plain</span>
				<input name='style' type='radio' disabled value='jqueryui' checked={viewSettings.inlineEdit.style === 'jqueryui' || !viewSettings.inlineEdit.style} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'style')} /> <span className='radio-label'>jQuery UI</span>
				<input name='style' type='radio' disabled value='bootstrap3' checked={viewSettings.inlineEdit.style === 'bootstrap3'} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'style')} /> <span className='radio-label'>Bootstrap 3</span>
			</div>
			<div className='pure-form settings-row'>
				<label className='inline-label'>
					<span>Edit Mode</span>
				</label>
				<input name='editMode' type='radio' disabled value='popup' checked={viewSettings.inlineEdit.editMode === 'popup' || !viewSettings.inlineEdit.editMode} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'editMode')} /> <span className='radio-label'>Popup</span>
				<input name='editMode' type='radio' disabled value='inline' checked={viewSettings.inlineEdit.editMode === 'inline'} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'editMode')} /> <span className='radio-label'>Inline</span>
			</div>

			<div className='pure-form settings-row'>
				<label className='inline-label'>
					<span>Enable Edit Label</span>
				</label>
				<input name='enableLabel' type='text' disabled value={viewSettings.inlineEdit.enableLabel} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'enableLabel')} />
			</div>
			<div className='pure-form settings-row'>
				<label className='inline-label'>
					<span>Disable Edit Label</span>
				</label>
				<input name='disableLabel' type='text' disabled value={viewSettings.inlineEdit.disableLabel} onChange={(e) => viewSettingsProps.saveViewSetting(e, 'inlineEdit', 'disableLabel')} />
			</div>
		</div>
	);
};

export default InlineEditSettings;
